.contacts-block{
	float: right;
}
.contacts{		
	&-phone, &-adress{
		display: inline-block;
		&:before{
			width: 12px;
			height: 16px;
			content: "";
			float: left;
		}
		&__content{		
			color: black;
			text-decoration: none;	
		}
	}
	&-phone{
		font-size: 19px;
		margin-left: 83px;
		&:before{
			background-image: url(/img/icons/telephone.png);
			margin: 7px 3px 0 0;
		}
	}
	&-adress{
		font-size: 14px;
		&:before{
			background-image: url(/img/icons/map-pin.png);
			margin: 4px 10px 0 0;
		}
	}
}

// $position: 'header';
// $icon_size: 26px;

$border-header: 2px;

.header{
	background-color: #fff;
	border-bottom: $border-header solid #ECEEF0;
	@include socials_icon('header', 26px, 12px, 0, 0, 0, 0);
	&__wrapper, &2__wrapper{
		padding: 25px 0 21px 0;
	}
	&2__wrapper{
		padding: 29px 0 24px 0;
	}
}
.social-text{
	@extend %texthide;
}
.header2{
	&_logo, &_search, &-meny-button{
		display: inline-block;		
	}
	&_logo{		
		width: 238px;
		height: 36px;
		// margin: 29px 0 29px 0;
	}	
	&_search{
		// margin: 30px 56px;
		vertical-align: top;
    	margin-left: 52px;
    	position: relative;
	}
	&-meny__list{
		float: right;
		list-style: none;
		margin: 7px 0 0 0;
		padding: 0;		
	}
	&-meny-button{
		font-size: 16px;
		margin: 0 0 0 29px;
		&:first-child{
			margin: 0;
		}
		&__content{
			font-family: opensans-semibold;
			color: black;
			&:hover{
				color: #FF6E2D;
			}
		}
	}
}
.input-search{
	font-size: 14px;
	padding: 8px 17px;
	width: 367px;
	height: 34px;
	border: 1px solid #dee1e4;
	border-radius: 4px;
}
.button-search{
	font-family: opensans-semibold;
	font-size: 17px;
	padding: 5px 0;
	position: absolute;
	width: 79px;
	margin: -34px 0px 0 288px;
	height: 34px;
	color: #fff;
	background-color: #FF6E2D;
	border: 0;
	border-radius: 0 4px 4px 0;}

